	module.exports = {"data":{"markdownRemark":{"html":"<h2>Adobe Experience Design</h2>\n<p>Adobe Experience Design, also known as Adobe XD, is a prototyping tool for UX designers. It’s a tool that is specifically built for interface design and user experience, unlike other Adobe products like Photoshop and Illustrator which were meant for photo editing and graphic design. Adobe XD is available for both Windows and macOS, unlike Sketch which is exclusive to macOS. </p>\n<h3>Advantages:</h3>\n<ul>\n<li>in-built prototyping tools</li>\n<li>available on both Windows and macOS</li>\n<li>mobile device preview</li>\n<li>free UI kits</li>\n<li>very clean and neat interface, unlike Photoshop. </li>\n<li>easy for Sketch users to switch as interface is very similar</li>\n<li>repeat-grid feature</li>\n<li>directly include Illustrator and Photoshop assets</li>\n</ul>\n<h3>Disadvantages</h3>\n<ul>\n<li>no plugins available, unlike Sketch which has a huge ecosystem already in place</li>\n<li>limited prototyping options; lacks many features of dedicated prototyping tools like InVision or Framer</li>\n</ul>\n<h4>More Information:</h4>\n<p>Official Website: <a href='https://www.adobe.com/in/products/experience-design.html' target='_blank' rel='nofollow'>Adobe</a></p>\n<p>XD Guru: <a href='https://www.xdguru.com/' target='_blank' rel='nofollow'>xdguru.com</a></p>\n<p>Adobe XD vs Sketch: <a href='https://www.sitepoint.com/adobe-xd-sketch-will-result-best-ux/' target='_blank' rel='nofollow'>Sitepoint</a></p>\n<p>Why Prototyping with Adobe XD is the Most-Complete Design Solution: <a href='https://www.sitepoint.com/prototyping-with-adobe-xd/' target='_blank' rel='nofollow'>Sitepoint</a></p>\n<p>And here’s a playlist of 36 videos to help you get started in Adobe XD: <a href='https://youtu.be/HqQtYIMnWhM' target='_blank' rel='nofollow'>Youtube</a></p>\n<p><a href='https://en.wikipedia.org/wiki/Adobe_Experience_Design' target='_blank' rel='nofollow'>Adobe Experience Design wikipedia page</a></p>","fields":{"slug":"/designer-tools/Experience-design/"},"frontmatter":{"title":"Adobe Experience Design"}}},"pathContext":{"slug":"/designer-tools/Experience-design/"}}

